<template>
  <section class="courseEdit">
    <div class="inner">
      <h1 class="tit">강의 수정</h1>
      <div class="snb">
        <course-nav />
      </div>
      <div class="board-box board-write">         
        <form id="inquiry" name="inquiry" method="" action="">
          <div class="box">
            <p class="form-tit">일괄 업로드</p>
            <button type="button" class="ty3 mgr10">엑셀 양식 다운로드</button>
            <button type="button" class="ty3 mgt10">엑셀 양식 업로드</button>
          </div>
          
          <div class="box rel">
            <p class="form-tit">개별 업로드</p>
            <table v-for="(week, index) in weeks" :key="index">              
              <colgroup>
                <col style="width:25%">
                <col style="width:75%">
              </colgroup>
              <tr>
                <th>주차수</th>
                <td>
                  {{ week }}주차
                </td>
              </tr>
              <tr>
                <th>학습 내용</th>
                <td>
                  <input type="text" class="w100">
                </td>
              </tr>
              <tr>
                <th>교재 범위</th>
                <td>
                  <input type="text" class="w100">
                </td>
              </tr>
              <tr>
                <th>수업 방법</th>
                <td>
                  <input type="text" class="w100">
                </td>
              </tr>
              <tr>
                <th>강의자</th>
                <td>
                  <input type="text" class="w100">
                </td>
              </tr>
              <tr>
                <th>키워드</th>
                <td>
                  <input type="text" class="w100">
                </td>
              </tr>
              <tr>
                <th>과제</th>
                <td>
                  <input type="text" class="w100">
                </td>
              </tr>
              <tr>
                <th>확인 문항</th>
                <td>
                  <input type="text" class="w100">
                </td>
              </tr>              
            </table>
            <button @click="addWeek" type="button" class="abs add-btn ty2">주차 추가 +</button>
          </div>
        </form>
        <div class="ea tc">
          <button>저장</button>
        </div> 
      </div>          
    </div>
  </section>
</template>

<script>
import CourseNav from '@/components/lms/sub/course/CourseNav.vue';

export default {
  components: { CourseNav },
  data() {
    return {
      weeks: [1], // Initial weeks
    };
  },
  methods: {
    addWeek() {
      const nextWeek = this.weeks.length + 1;
      this.weeks.push(nextWeek);
    },
    removeWeek(index) {
      // Ensure that there is at least one week
      if (this.weeks.length > 1) {
        this.weeks.splice(index, 1);
      }
    },
  },
};
</script>

<style scoped lang="scss">
.add-btn {top:.2rem;right:0;height:3rem;line-height:2.9rem;}
.box {
  margin-top:5rem;
  table {margin-top:0;} 
}
@media (max-width: #{$tablet - 1px}) {
  .board-box .w40 {
    width:100%;
  }
  .box {
    margin-top:6rem;
    table {margin-top:0;} 
  }
}
</style>